
<template>
  <div class="apartat">
  <!-- formulari per a que els visitans de la pagina puguin concretar una cita -->
    <div class="formulari">
      <b-form @submit="onSubmit" @reset="onReset" v-if="show">
        <!-- peticio correu electronic -->
        <b-form-group
          id="input-group-1"
          label="Correu electrònic:"
          label-for="input-1"
          description="No compartirem el seu correu amb ningú.">
          <b-form-input
            id="input-1"
            v-model="form.email"
            type="email"
            placeholder="Introdueixi el seu correu"
            required></b-form-input>
        </b-form-group>
        <b-form-group id="input-group-2" label="El seu nom:" label-for="input-2">
          <!-- peticio nom -->
          <b-form-input
            id="input-2"
            v-model="form.name"
            placeholder="Introduiexi el seu nom complet"
            required></b-form-input>
        </b-form-group>
        <b-form-group id="input-group-3" label="Elegeixi una data:" label-for="input-3">
          <!-- peticio data de la cita -->
            <b-form-datepicker v-model="form.datep" :date-disabled-fn="dateDisabled" label="datep"></b-form-datepicker>
        </b-form-group>
        <b-form-group id="input-group-4" label="Elegeixi una hora:" label-for="input-4">
          <!-- peticio hota de la cita -->
          <b-form-timepicker
            id="timepicker-buttons"
            v-model="form.timep"
            label="timep"
            now-button
            reset-button
            :hour-disabled-fn="hourDisabled"
            ></b-form-timepicker>
         </b-form-group>
        <b-form-group id="input-group-5" label="Gatet:" label-for="input-5">
          <!-- peticio sobre quin gatet volen consultar -->
          <b-form-select
            id="input-3"
            v-model="form.gatet"
            :options="gatets"
            required></b-form-select>
        </b-form-group>  
        <b-form-group id="input-group-6" v-slot="{ ariaDescribedby }">
          <!-- peticio si volen rebre correus nostres -->
          <b-form-checkbox-group
            v-model="form.checked"
            id="checkboxes-4"
            :aria-describedby="ariaDescribedby">
            <b-form-checkbox value="me"><p class="box">Li agradaria rebre correus informatius sobre els nostres gatets?</p></b-form-checkbox>
          </b-form-checkbox-group>
        </b-form-group>
          <!-- botons per borrar o enviar el formulari -->
        <b-button  @click="showMsgBoxOne"  type="submit" variant="primary">Enviar</b-button>
        <b-button @click="showMsgBoxTwo" type="reset" variant="danger">Esborrar</b-button>
      </b-form>
    </div>
  </div>
  </template>
  
  <script>
    export default {
      data() {
        return  {
          form: {
            //formulari al inici
            email: '',
            name: '',
            gatet: null,
            checked: [],
            datep: '',
            timep: ''
        },
          //opcions del desplegable
          gatets: [{ text: 'Select One', value: null }, 'Firulais', 'Tom', 'Baldufa', 'Tina', 'Cleo', 'Namaste', 'Lluna', 'Paquita', 'Cendra', 'Pruna', 'Tiger', 'Luigi'],
          show: true
        }
      },
      methods: {
        dateDisabled(ymd, date) {
        const weekday = date.getDay()
        const day = date.getDate()
        // bloquejem els dies dissabte i diumenge
        return weekday === 0 || weekday === 6
      },
    showMsgBoxOne() {
        this.boxTwo = ''
        //confirmacio de la cita
        this.$bvModal.msgBoxOk('Cita concretada correctament', {
          title: 'Confirmació',
          size: 'sm',
          buttonSize: 'sm',
          okVariant: 'success',
          headerClass: 'p-2 border-bottom-0',
          footerClass: 'p-2 border-top-0',
          centered: true
        })
          .then(value => {
            this.boxTwo = value
          })
          .catch(err => {
            // An error occurred
          })
      },
    showMsgBoxTwo() {
        this.boxTwo = ''
        //confirmacio de que s'ha borrat el formulari
        this.$bvModal.msgBoxOk('Formulari esborrat correctament', {
          title: 'Confirmació',
          size: 'sm',
          buttonSize: 'sm',
          okVariant: 'success',
          headerClass: 'p-2 border-bottom-0',
          footerClass: 'p-2 border-top-0',
          centered: true
        })
          .then(value => {
            this.boxTwo = value
          })
          .catch(err => {
            // An error occurred
          })
      },
        onSubmit(event) {
          event.preventDefault()
          // posar els valors a 0
          this.form.email = ''
          this.form.name = ''
          this.form.gatet = null
          this.form.checked = []
          this.form.datep= ''
          this.form.timep= ''
          // Trick to reset/clear native browser form validation state
          this.show = false
          this.$nextTick(() => {
          this.show = true
          })
        },
        onReset(event) {
          event.preventDefault()
          // posar els valors a 0
          this.form.email = ''
          this.form.name = ''
          this.form.gatet = null
          this.form.checked = []
          this.form.datep= ''
          this.form.timep= ''
          // Trick to reset/clear native browser form validation state
          this.show = false
          this.$nextTick(() => {
          this.show = true
          })
        }
      }
    }
  </script>
<style scoped>

.formulari {
  display: flex;
  justify-content: center;
}
form {
  width: 70%;
}

.box {
  position: left;
  margin-left: 25px!important;
}
.btn-danger {
  color: #fff;
  background-color: #dc3545;
  border-color: #dc3545;
  margin-left: 1em;
}
.btn-primary {
  color: #fff;
  margin-right: 1em;
  background-color: #007bff;
  border-color: #007bff;
}
</style>
  