<template>
    <div class="adopta" aria-label="subpagina amb els gats que es poden adoptar">
      <!-- titol de la subpagina -->
      <h2 class="pb-2">
       <icons :icon="['fas', 'award']" /> 
        ELS NOSTRES GATS
       <icons :icon="['fas', 'award']" /> 
       </h2>    
       <!-- classe que conte els cards amb les dades del .json -->
        <div class="container-md">
            <b-card-group deck> 
              <div columns v-for="proyecto in proyectos" :key="proyecto.id">
               <Card2 :temps="proyecto.temps" :srcimg="proyecto.srcimg" :alt="proyecto.alt" :nom="proyecto.nom" :edat="proyecto.edat" :url="proyecto.url"></Card2> 
              </div>
             </b-card-group>
        </div>
        <div class="apartat">        
          <h2>Tens dubtes sobre algun dels nostres gatets?</h2>
          <p>Omple el formulari següent per vindre a visitar-ns i poder conèixer al petit que més t'agradi.</p>
        
        <!-- cridem el formulari de baix de tot -->
          <Form/>
        </div>
   </div>
  
  </template>
  <style scoped>
  /*estil aplicat a la vista i al conjunt de cards*/
  .adopta{
    margin:2em;
  }
  .card-deck {
    display: flex;
    justify-content: center;
    row-gap: 1.2em;
  }
  .apartat {
    margin-top:2em;
  }
  
  </style>
  <script>
  // importem els components
  import Card2 from '../components/Card2Comp.vue'; 
  import Form from "../components/FormComp.vue";
  // vinculem el .json que hem guardat a la carpeta public
  const lstProysJson = 'https://raw.githubusercontent.com/raf110/miau/main/Algo.json';
  // script per a que la view ompli el component card2comp.vue amb el contingut del .json
  export default {
    props: {  
     name: {
        type: String,
        default: 'ListaProyectos'
      }
    },
    components: {
          Card2,
          Form
        },
    data() {
        return {
          proyectos:[],
          githubPage: 1,
          page: 1,
          loading: false,
          perPage: 5
        }
      },
      mounted() {
        this.fetchData()
      },
      methods: {
          async fetchData() {
            const res = await fetch (lstProysJson);
            const val= await res.json();
            this.proyectos=val;
            console.log(val);
          }
      }
    }
  </script>
